.notfound {
    margin-top: 16rem;

    @media(max-width: 700px) {
        margin-top: 5rem;            
    }
    
    &__heading {
        font-size: 6.2rem;
        text-align: center;
        opacity: 0.7;
        font-weight: 200;
        margin-bottom: 4rem;

        @media(max-width: 700px) {
            padding-left: 5px;
            font-size: 4rem;            
        }
    }

    &__paragraph {
        font-size: 1.8rem;
        text-align: center;
        margin-bottom: 14rem;

        @media(max-width: 700px) {
            padding-left: 5px;
            font-size: 1rem;  
            margin-bottom: 5rem;          
        }
    }
}