.listingform {
    &__section {
        display: flex;
        flex-direction: column;
        justify-content:space-between;
        align-items: flex-start;
        // padding-bottom: 20px;
    }

    &__label {
        font-size: 1.6rem;
        font-weight: bolder;
        color: #45171d;
        margin-bottom: 0.8rem;

        @media(max-width: 700px){
            font-size: 1.2rem;
        }
    }

    &__select {
        font-size: 1.6rem;
        width: 100%;
        height: 3rem;
        outline: none;
        margin-bottom: 1.5rem;
        cursor: pointer;
        padding: 5px;
        color: #45171d;
        background-color: #fecea8;
        border-radius: 5px;
        border: 1px solid #dddddd;

        @media(max-width: 700px){
            font-size: 1.2rem;
        }

        &:focus {
            outline: none;
            border: 1px solid #fff;
            
        }

        &:hover {
            outline: none;
            border: 1px solid #000;
        }

        // option {
        //     background: #ffffff
        // }
    }

    &__input {
        width: 100%;
        height: 3rem;
        font-size: 1.6rem;
        padding: 0.5rem 0.8rem;
        outline: none;
        cursor: pointer;
        background-color: #fecea8;
        border-radius: 5px;
        border: 1px solid #dddddd;

        &:hover {
            outline: none;
            border: 1px solid #000;
        }
    }

    &__button {
        display: flex;
        justify-content: center;
        padding: 1rem 2rem;
        font-size: 1.8rem;
        font-weight: bolder;
        color: $color-white;
        margin-top: 2rem;
        border-radius: 0.5rem;
        box-shadow: 0.1rem 0.2rem 0.3rem rgba(0,0,0,0.2);
        cursor: pointer;

        &:hover {
            background-color: #86313c;
        }

        &--primary {
            background-color: #45171d;
            transition: 0.5s;
        }

    }

    &__loader {
        padding-top: 2rem;
        display: flex;
        // justify-content: center;
        // align-items: center;
    }
}
