.navbar{
    width: 100%;
    
    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: 8rem;
        background-color: #45171d;

        @media(max-width: 700px){
            align-items: flex-start;
            display: flex;
            flex-direction: column;
            height: auto;
            justify-content: end;
            // margin:-10px;
        }

        &__logo {
            margin-left: 2.8rem;
            
            @media(max-width: 700px){
                margin-left: 1rem;
            }

            &::after {
                content: '';
                width: 0%;
                height: 2px;
                background: #f33446;
                display: block;
                margin: auto;
                transition: 0.5s;
            }

            &:hover::after {
                width: 100%;
            }

            &__link {
                font-size: 4rem;
                font-weight: bold;
                font-family: "Times New Roman", Times, serif;
                color: #fecea8;
                text-decoration: none;
                display: flex;
                flex-direction: row;

                @media(max-width: 700px){
                    display: flex;
                    flex-direction: column;
                    justify-content: start;
                    font-size: 30px;
                    padding: 10px;
                    left:0;
                }
            }
        }
        
        &__auth {
            flex:1;
            text-align: right;

            @media(max-width: 700px){
                text-align: left;
            }
    

            &__ul{
                padding: 30px;

                @media(max-width: 700px){
                    padding-top: 50px;
                    padding-left: 0;

                }
        
            }

            &__item {
                list-style: none;
                display: inline-block;
                padding: 8px 25px;
                position: relative;

                &::after {
                    content: '';
                    width: 0%;
                    height: 2px;
                    background: #f33446;
                    display: block;
                    margin: auto;
                    transition: 0.5s;
                }

                &:hover::after {
                    width: 100%;

                    @media(max-width: 700px){
                        width:0;
                    }
            
                }

                @media(max-width: 700px){
                    display: block;
                    position: relative;
                }
        
            }

            &__link {
                color: #fecea8;
                font-weight: bolder;
                text-decoration: none;
                font-size: 16px;
            }
        }
    }
}
