.listingdetail {
    // padding-bottom: 4rem;

    &__header {
        background-color: rgba($color-primary, 0.5);
        padding-top: 4rem;
        padding-bottom: 4rem;
        text-align: center;
    }

    &__title {
        font-size: 4.2rem;
        font-weight: bolder;
        color: #fdffcd;
        margin-bottom: 2rem;

        @media(max-width: 700px) {
            font-size: 2.4rem;
        }
    }

    &__location {
        font-size: 2.4rem;
        font-weight: bolder;
        color: #fdffcd;

        @media(max-width: 700px) {
            font-size: 1.4rem;
        }
    }

    &__breadcrumb {
        background-color: $color-grey-light;
        padding: 2rem 4rem;
        font-size: 1.6rem;
        margin-top: 2rem;

        @media(max-width: 700px) {
            font-size: 1.2rem;
        }

        &__link {
            color: $color-primary;
            text-decoration: none;

            &:hover {
                color:$color-tertiary;
            }
        }
    }

    &__display {
        width: 100%;

        &__image {
            width: 100%;
            border-radius: 10px;
        }
    }

    &__realtor {
        font-size: 2.4rem;
        color: $color-primary;
        margin-top: 2rem;
        margin-bottom: 1rem;

        @media(max-width: 700px) {
            margin: 1rem;
            padding-left: 5px;
            font-size: 1.4rem;            
        }
    }

    &__contact {
        font-size: 1.6rem;
        color: $color-tertiary;

        @media(max-width: 700px) {
            margin: 1rem;
            padding-left: 5px;
            font-size: 1.4rem;            
        }
    }

    &__about {
        font-size: 1.6rem;
        margin-top: 1rem;

        @media(max-width: 700px) {
            margin: 1rem;
            padding-left: 5px;
            font-size: 1.2rem;
            padding-bottom: 4rem;            
        }
    }

    &__list {
        padding-top: 20px;
        list-style: none;
        font-size: 1.7rem;
        color: #8b104e;
        font-weight: bolder;

        @media(max-width: 700px) {
            font-size: 1.2rem;
            font-weight: normal;
            padding-top:10px;
        }

        &__item {
            margin-bottom: 1rem;

            @media(max-width: 700px) {
                margin-bottom: 0.5rem;
            }            
        }
    }

    &__description {
        font-size: 1.6rem;

        @media(max-width: 700px) {
            font-size: 1rem;
            text-align: justify;
        }
    }
}

.col-1-of-4 {
    @media(max-width: 700px) {
        margin-top:-300px;
    }
}