.about {
    &__header {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 8rem 0;
        background-color: rgba($color-primary, 0.5);

        @media(max-width: 700px) {
            padding: 3rem 0;
        }
    }

    &__info {
        margin-top: 4rem;
    }

    &__team {
        margin-top: 4rem;
        margin-bottom: 6rem;
    }

    &__heading {
        font-size: 6rem;
        font-family: "Times New Roman", Times, serif;
        color: #fdffcd;
        font-weight: bold;

        @media(max-width: 700px) {
            font-size: 2.4rem;
        }
    }

    &__subheading {
        font-size: 3.2rem;
        color: $color-primary;
        font-weight: 200;
        margin-bottom: 2rem;

        @media(max-width: 700px) {
            font-size: 1.8rem;
            font-weight: bolder;
        }
    }

    &__paragraph {
        font-size: 1.6rem;
        color: $color-paragraph;
        margin-top: 1rem;
        margin-bottom: 1.5rem;
        text-align: justify;

        @media(max-width: 700px) {
            font-size: 1.2rem; 
            position: relative;

            &:last-child{
               padding-bottom: 20rem;
            }       
        }
    }

    &__display {
        width: 100%;

        &__image {
            width: 100%;
            border-radius: 15px;
        }
    }

    &__realtor {
        font-size: 1.8rem;
        font-weight: 200;
        color: $color-primary;
        margin-top: 1rem;
        margin-bottom: 1rem;

        @media(max-width: 700px) {
            margin-left: 10px;
            font-size: 1.4rem; 
        }        
    }

    &__topseller {
        font-size: 2.4rem;
        color: $color-paragraph;
        font-weight: bolder;
        margin-top: 2rem;
        padding:0 10px 10px 0;

        @media(max-width: 700px) {
            font-size: 1.8rem; 
        }
    }

    &__contact {
        font-size: 1.6rem;
        color:$color-tertiary;
        margin-bottom: 0.5rem;

        @media(max-width: 700px) {
            margin-left: 10px;
            font-size: 1.4rem; 
        }
    }

    &__about {
        font-size: 1.6rem;
        color: $color-paragraph;
        margin-top: 1rem;

        @media(max-width: 700px) {
            margin-left: 10px;
            font-size: 1rem; 
            padding-bottom: 20px;
        }
    }
}

.row{
    @media(max-width: 700px) {
        margin-bottom: 0;
    }    
}