.auth{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    &__title {
        font-size: 4rem;
        color: $color-primary;
        margin-top: 4rem;
        margin-bottom: 3rem;

        @media(max-width: 700px) {
            font-size: 2.2rem;            
            margin-bottom: 1.5rem;
        }
    }

    &__lead {
        font-size: 2.4rem;
        color: $color-secondary;
        margin-bottom: 2rem;

        @media(max-width: 700px) {
            font-size: 1.4rem;            
        }
    }

    &__form {
        &__group {
            margin-bottom: 1rem;
        }

        &__input {
            font-size: 1.6rem;
            padding: 1rem 1.5rem;
            outline-color: $color-primary;

            @media(max-width: 700px) {
                font-size: 1.2rem;            
            }
        }

        &__button {
            font-size: 1.8rem;
            width: 70%;
            margin-left: 10%;
            background-color: $color-white;
            color: $color-primary;
            border: 0.1rem solid $color-primary;
            border-radius: 0.2rem;
            cursor: pointer;
            margin-top: 1rem;
            margin-bottom: 2rem;
            outline: none;
            padding: 3px ;
            align-items: center;
            transition: 0.5s;
            
            @media(max-width: 700px) {
                font-size: 1.4rem;  
                padding: 2px;     
            }

            &:hover {
                color:$color-white;
                background-color: $color-primary;
            }
        }
    }

    &__authtext {
        font-size: 1.6rem;
        color: $color-paragraph;

        @media(max-width: 700px) {
            font-size: 1.2rem;   
            padding-bottom: 2rem;         
        }
    
        &__link {
            text-decoration: none;
            color: $color-primary;
    
            &:hover {
                color: $color-link-hover;
            }
        }
    }
}