.card {
    padding: 4rem 3rem;
    background-color: $color-grey-light;
    border-radius: 0.5rem;

    &__header {
        width: 100%;

        &__photo {
            border: 1px solid;
            width: 100%;
            border-radius: 3px;
        }
    }

    &__title {
        color: #bb7171;
        font-size: 3rem;
        font-weight: bolder;
        margin-bottom: 2rem;

        @media(max-width: 700px){
            font-size: 1.6rem;
        }
    }

    &__location {
        font-size: 1.6rem;
        margin-top: 2rem;
        margin-bottom: 2rem;
        color: #45171d;


        @media(max-width: 700px){
            font-size: 1rem;
        }
    }

    &__info {
        font-size: 1.6rem;
        color: #591fce;
        font-weight: bolder;
        margin-bottom: 0.5rem;

        @media(max-width: 700px){
            font-size: 1.2rem;
        }
    }

    &__saletype {
        font-size: 1.6rem;
        font-weight: bolder;
        color: #591fce;
        margin-bottom: 0.5rem;

        @media(max-width: 700px){
            font-size: 1.2rem;
        }
    }

    &__hometype {
        font-size: 1.6rem;
        font-weight: bolder;
        color: #8b104e;
        margin-bottom: 0.5rem;

        @media(max-width: 700px){
            font-size: 1.2rem;
        }
    }

    &__sqft {
        font-size: 1.4rem;
        color:#45171d;
        font-weight: bolder;

        @media(max-width: 700px){
            font-size: 1.2rem;
        }
    }

    &__link {
        font-size: 1.6rem;
        color: $color-secondary;
        padding: 1rem 1.5rem;
        border: 0.1rem solid $color-secondary;
        text-decoration: none;

        @media(max-width: 700px){
            font-size: 1.2rem;
        }

        &:hover {
            color: $color-white;
            border: 0.1rem solid $color-secondary;
            background-color: $color-primary;
        }
    }
}